	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Gliding","date":"05 Apr 2016","slug":"/2016-04-05-gliding/","tags":"Y2016"},"html":"<blockquote class=\"instagram-media\" data-instgrm-captioned data-instgrm-version=\"6\" style=\" background:#FFF; border:0; border-radius:3px; box-shadow:0 0 1px 0 rgba(0,0,0,0.5),0 1px 10px 0 rgba(0,0,0,0.15); margin: 1px; max-width:658px; padding:0; width:99.375%; width:-webkit-calc(100% - 2px); width:calc(100% - 2px);\"><div style=\"padding:8px;\"> <div style=\" background:#F8F8F8; line-height:0; margin-top:40px; padding:37.5% 0; text-align:center; width:100%;\"> <div style=\" background:url(data:image/png;base64,); display:block; height:44px; margin:0 auto -44px; position:relative; top:-22px; width:44px;\"></div></div> <p style=\" margin:8px 0 0 0; padding:0 4px;\"> <a href=\"https://www.instagram.com/p/BDvxkgOo_e-/\" style=\" color:#000; font-family:Arial,sans-serif; font-size:14px; font-style:normal; font-weight:normal; line-height:17px; text-decoration:none; word-wrap:break-word;\" target=\"_blank\">Great shot by my flight instructor @simon_bacher 42 minutes in blue thermals and lots of spinning ðŸ˜°ðŸ˜… #segelfliegen #gliding #soaring #thermik #blauthermik #thermals #900m #wenzendorf #ask13 #d8964 #segelflugzeug #glider #sailplane #gopro #gopropilots_ #sobock #soobock</a></p> <p style=\" color:#c9c8cd; font-family:Arial,sans-serif; font-size:14px; line-height:17px; margin-bottom:0; margin-top:8px; overflow:hidden; padding:8px 0 7px; text-align:center; text-overflow:ellipsis; white-space:nowrap;\">A photo posted by Christopher Hanson (@fseddh) on <time style=\" font-family:Arial,sans-serif; font-size:14px; line-height:17px;\" datetime=\"2016-04-03T17:27:53+00:00\">Apr 3, 2016 at 10:27am PDT</time></p></div></blockquote>\n<script async defer src=\"//platform.instagram.com/en_US/embeds.js\"></script>"},"next":{"fields":{"title":"Where and For Whom to Vote","date":"19 Feb 2016","slug":"/2016-02-19-where-and-for-whom-to-vote/"}},"previous":{"fields":{"title":"Student Exchange","date":"05 Apr 2016","slug":"/2016-04-05-student-exchange/"}}},{"node":{"fields":{"title":"Where and For Whom to Vote","date":"19 Feb 2016","slug":"/2016-02-19-where-and-for-whom-to-vote/","tags":"Y2016"},"html":"<p>Like <a href=\"https://en.wikipedia.org/wiki/Will_Rogers\">Will Rogers</a>, I am proud to not be a member of any organized political party. I am a Democrat.</p>\n<p>And true to that spirit, as a Democrat living outside the United States, for the current presidential campaign I not only have to decide for whom to vote, but also where. (For my foreign readers following the US election, yes the process is complicated, and my description will make the impression that it is even more complicated. That is deliberate on my part.)</p>\n<p>I can vote (in my last state of residence) in the Arizona presidential primary on March 22 (which, by the way, is not a primary but instead a <a href=\"http://www.azsos.gov/elections/voting-election/election-information\">presidential preference election</a>, since the actual primary for state and local candidates is in August). However, the Democrats have an organization for Democrats living abroad (called, appropriately enough, <a href=\"http://www.democratsabroad.org/\">Democrats Abroad</a>) which is entitled to send delegates to the Democratic National Convention. Eight of those delegates are superdelegates, beholden to no one but themselves, but thirteen delegates are pledged. And to choose those pledged delegates, they hold the <a href=\"http://www.democratsabroad.org/the_global_presidential_primary_is_now_vote\">Democrats Abroad Global Presidential Primary</a>.</p>\n<p>And just like in an official presidential primary (or preference election), I can even go to a real polling place to vote. In Hamburg! On March 5! At the <a href=\"http://www.democratsabroad.org/848/hamburg_global_presidential_primary\">Friedrich Ebert Stiftung on Rathausmarkt!</a> (hold on, are we in cahoots with the German SPD on this?) Or at a number of <a href=\"http://www.democratsabroad.org/bseebach/democrats_abroad_germany_global_primary_locations\">other locations in Germany</a>, or even <a href=\"https://d3n8a8pro7vhmx.cloudfront.net/democratsabroad/pages/4823/attachments/original/1454667653/VotingCenters2016.pdf\">around the world</a>. If wasn't already a member of Democrats Abroad, I could join on-site at the polling place. If I couldn't make it to a polling place, I could vote remotely, by email, fax or snail mail.</p>\n<p>So while I could vote in my former home state of Arizona, it makes more sense for me to vote in the global primary, since we Americans abroad have unique issues for our candidates to be aware of (like <a href=\"http://www.howtogermany.com/pages/ustaxes.html\">taxation</a>, and <a href=\"https://en.wikipedia.org/wiki/Foreign_Account_Tax_Compliance_Act\">FATCA</a>, and taxation).</p>\n<p>So now I know where I will vote. Now I just have to decide for whom.</p>\n<p>(In all fairness, there is also an organization for <a href=\"https://www.facebook.com/republicansoverseas/\">Republicans abroad</a>, but you guys don't get a primary. You're not even officially part of the Republican party. Sorry.)</p>"},"next":{"fields":{"title":"Will You Still Love Me When I'm Fifty-Four?","date":"13 Feb 2016","slug":"/2016-02-13-will-you-still-love-me-when-im-fiftyfour/"}},"previous":{"fields":{"title":"Gliding","date":"05 Apr 2016","slug":"/2016-04-05-gliding/"}}},{"node":{"fields":{"title":"Will You Still Love Me When I'm Fifty-Four?","date":"13 Feb 2016","slug":"/2016-02-13-will-you-still-love-me-when-im-fiftyfour/","tags":"Y2016"},"html":"<p><img src=\"/assets/fifty-four-130216.jpg\" alt=\"Aged 54 years\"></p>\n<p>I guess the answer is yes!</p>"},"next":{"fields":{"title":"Anniversary","date":"06 Sep 2015","slug":"/2015-09-06-anniversary/"}},"previous":{"fields":{"title":"Where and For Whom to Vote","date":"19 Feb 2016","slug":"/2016-02-19-where-and-for-whom-to-vote/"}}}],"pathPrefix":"","first":false,"last":false,"index":9,"pageCount":1101}}

